.contacts__right {
  height: 400px;
}

.contacts__tel {
  display: block;

  margin-bottom: 20px;

  text-decoration: none;

  font-size: 24px;
  font-weight: 700;
  line-height: 1.2em;

  color: $black;
}

.contacts__callback {
  margin-bottom: 30px;
  padding: 12px 35px;

  cursor: pointer;

  border: 1px solid rgba(43, 43, 43, 1);
  border-radius: 6px;
  background-color: $white;

  &:hover,
  &:focus {
    border-color: $yellow;
    background-color: $yellow;
  }
}

.contacts__item {
  margin-bottom: 30px;

  font-size: 16px;
}

.contacts__info {
  margin: 0;
  margin-bottom: 12px;

  text-decoration: none;

  color: $black;

  span {
    display: block;

    line-height: 1.5em;
  }

  &--link {
    &:hover,
    &:focus {
      color: $yellow;
    }
  }
}

.contacts__sub-title {
  margin: 0;
  margin-bottom: 20px;
}

@media(min-width:$widthDesktop) {
  .contacts__title {
    margin-bottom: 40px;
  }

  .contacts {
    display: grid;

    grid-template-columns: minmax(0, 258px) minmax(0, 800px);
    justify-content: space-between;
  }
}
