.order__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.order__item {
  margin-bottom: 30px;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.order__number {
  display: flex;

  width: 58px;
  height: 58px;
  margin-bottom: 20px;

  font-size: 35.7px;

  border-radius: 50%;
  background-color: $yellow;

  align-items: center;
  justify-content: center;
}

@media(min-width:$widthTablet) {
  .order__text {
    font-size: 16px;
  }

  .order__title {
    margin-bottom: 98px;
  }

  .order__number {
    margin-bottom: 30px;
  }

  .order__list {
    display: grid;

    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 42px;
  }

  .order__item {
    position: relative;

    &::before {
      position: absolute;
      top: 25px;
      right: 0;

      width: calc(100% - 88px);
      height: 15px;

      content: '';

      background: url('../img/arrow.svg') 0 0 no-repeat;
      background-size: contain;
    }

    &:last-of-type {
      &::before {
        display: none;
      }
    }
  }
}
