.lease__container {
  margin-bottom: 50px;
}

.lease__leased {
  margin-bottom: 40px;

  font-size: 24px;
}

.lease__label {
  display: flex;

  margin-bottom: 8px;

  align-items: center;
  gap: 10px;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.lease__price {
  max-width: 321px;

  border: 1px solid #828282;
  border-radius: 10px;
}

.lease__item {
  display: flex;

  margin-bottom: 20px;

  border: 1px solid rgba(130, 130, 130, 1);
  border-radius: 10px;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.lease__places {
  padding: 27px 30px 20px 30px;

  font-size: 18px;

  border-bottom: 1px solid rgba(130, 130, 130, 1);
}

.lease__place {
  display: flex;
  flex-direction: column;

  padding: 26px;

  font-size: 18px;
  font-weight: 700;
  line-height: 1.2em;

  border-right: 1px solid rgba(130, 130, 130, 1);

  gap: 10px;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  span {
    display: block;

    font-size: 24px;
  }
}

.lease__info {
  display: grid;

  width: 100%;
  padding: 28px;

  font-size: 16px;

  gap: 10px;
  align-items: center;
}

.lease__text {
  margin: 0;

  span {
    display: block;

    margin-bottom: 6px;

    font-size: 18px;
    font-weight: 700;
  }
}

.lease__total {
  padding: 18px 30px 30px 30px;
}

.lease__total-item {
  display: flex;

  margin-bottom: 30px;

  font-size: 24px;
  font-weight: 700;

  align-items: center;
  justify-content: space-between;
}

.lease__payment {
  font-size: 16px;
}

.lease__places-item {
  display: flex;

  margin-bottom: 14px;

  align-items: center;
  justify-content: space-between;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.lease__payment-title {
  margin: 0;
  margin-bottom: 12px;
}

.lease__payment {
  margin-bottom: 30px;
}

.lease__button {
  display: flex;

  max-width: 235px;
  padding: 11px;

  cursor: pointer;

  font-size: 18px;

  border: 2px solid rgba(43, 43, 43, 1);
  border-radius: 6px;
  background-color: $white;

  align-items: center;
  gap: 5px;

  svg {
    fill: none;
  }

  &:hover,
  &:focus {
    opacity: 0.7;
  }
}

.lease__pay {
  width: 100%;
}

.lease__left {
  margin-bottom: 30px;
}

.lease__price {
  margin: 0 auto;
}

.lease__add-more {
  max-width: 283px;

  text-decoration: none;
}

.lease__title {
  margin-bottom: 40px;
}

@media(min-width:$widthTablet) {
  .lease__info {
    grid-template-columns: 1fr 1fr minmax(0, 235px);
  }
}

@media(min-width:$widthDesktop) {
  .lease__container {
    display: grid;

    grid-template-columns: minmax(0, 818px) minmax(0, 321px);
    gap: 20px;
  }

  .lease__left {
    margin-bottom: 0;
  }

  .lease__price {
    margin: 0;
  }
}
