.about > .container {
  display: flex;
  flex-direction: column;

  gap: 20px;
}

.about__text {
  margin: 0;

  font-size: 16px;
  line-height: 1.5em;
}

.about__img {
  width: 100%;
  height: auto;

  object-fit: contain;
}

@media(min-width:$widthTablet) {
  .about > .container {
    flex-direction: row;

    gap: 40px;
  }
}
