.modal {
  &.hidden {
    display: none;
  }

  .two {
    display: none;
  }

  &.sent {
    .one {
      display: none;
    }

    .two {
      display: block;
    }
  }
}

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }

  70% {
    transform: translateY(30px);
  }

  90% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}

.modal__overlay {
  position: fixed;
  z-index: 99;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  display: flex;

  min-width: 320px;
  padding: 30px 0;

  background-color: rgba(24, 22, 22, 0.5);

  justify-content: center;
  align-items: center;
}

.modal__container {
  position: absolute;

  width: 95%;
  max-width: 561px;
  margin: 0 auto;
  padding: 30px 20px;

  animation: bounce 1s;

  background-color: $white;

  flex-shrink: 0;
}

.modal__button-close {
  position: absolute;
  top: 10px;
  right: 32px;

  width: 30px;
  height: 30px;
  padding: 0;

  cursor: pointer;

  border: none;
  outline: none;
  background-color: rgba(0, 0, 0, 0);
  background-repeat: no-repeat;
  background-position: center center;

  &::before {
    position: absolute;
    left: 0;

    width: 25px;
    height: 3px;

    content: '';
    transform: rotate(45deg);

    background-color: $black;
  }

  &::after {
    position: absolute;
    left: 0;

    width: 25px;
    height: 3px;

    content: '';
    transform: rotate(-45deg);

    background-color: $black;
  }
  // &:focus,
  // &:active,
  // &:hover {
  //   &::before,
  //   &::after {
  //     background-color: $green;
  //   }
  // }
}

.modal__heading {
  margin: 0;
  margin-bottom: 40px;

  font-size: 30px;
  font-weight: 700;
  line-height: 38.4px;
  // color: $green;
}

.modal__info {
  text-align: center;

  font-size: 0.9em;
  line-height: 2em;
}

.modal__text {
  .login {
    padding: 0;

    cursor: pointer;
    text-decoration: underline;

    font-family: $play;

    border: none;
    background-color: transparent;

    &:hover,
    &:focus {
      color: $yellow;
    }
  }
}

.modal__tick {
  margin: 0 auto;
  margin-bottom: 30px;
}

.modal__title {
  margin: 0;
  margin-bottom: 30px;

  font-size: 26px;
  font-weight: 700;

  color: $black;
}

.modal__name {
  margin: 0;
  margin-bottom: 46px;

  font-size: 16px;
  font-weight: 500;

  color: $black;

  span {
    display: block;

    width: 100%;

    text-align: center;
  }
}

.modal__label {
  &:last-of-type {
    .modal__input {
      margin-bottom: 30px;
    }
  }
}

.modal__input {
  width: 100%;
  margin-bottom: 20px;
  padding: 10px;

  resize: none;

  border: 1px solid #8a8a8a;
  border-radius: 6px;

  &::placeholder {
    font-family: 'Montserrat', 'Arial', sans-serif;
  }
}

textarea.modal__input {
  height: 171px;
}

.modal__submit {
  display: block;

  width: 100%;
  padding: 14px;

  cursor: pointer;
  text-align: center;
  text-decoration: none;

  font-size: 18px;

  color: $black;
  border-radius: 6px;
  background-color: $yellow;

  &.disabled {
    color: #666464;
    background-color: #e6e5e5;
  }
}

.modal__close {
  position: absolute;
  top: 10px;
  right: 10px;

  cursor: pointer;

  border: none;
  background-color: transparent;

  svg {
    stroke: $black;
  }

  &:hover,
  &:focus {
    opacity: 0.7;
  }
}

.modal__send-again {
  display: block;

  margin: 0 auto;
  margin-top: 20px;

  font-size: 16px;

  color: #377bc4;
  border: none;
  background-color: transparent;
}

@media(min-width:$widthDesktop) {
  .modal__container {
    padding: 40px 66px;
  }
}
