.footer {
  padding: 50px;

  background-color: $black;
}

.footer__logo {
  margin-bottom: 38px;
}

.footer__tel {
  display: block;

  margin-bottom: 20px;

  font-size: 24px;
  font-weight: 700;
  line-height: 27.77px;

  color: $white;
}

.footer__callback {
  width: 100%;
  margin-bottom: 50px;

  cursor: pointer;
  text-decoration: underline;

  font-size: 18px;

  color: $yellow;
  border: none;
  background-color: transparent;

  &:hover,
  &:focus {
    opacity: 0.8;
  }
}

.footer__link {
  display: block;

  margin-bottom: 30px;

  text-align: center;
  text-decoration: underline;
  text-transform: uppercase;

  color: #e1e1e1;

  &:last-of-type {
    margin-bottom: 0;
  }

  &:hover,
  &:focus {
    color: $yellow;
  }
}

.footer__logo {
  display: flex;

  justify-content: center;

  svg {
    width: 100%;
    max-width: 317px;
    height: auto;
  }
}

.footer__tel {
  text-align: center;
  white-space: nowrap;
}

@media(min-width: $widthTablet) {
  .footer__link {
    text-align: start;
  }

  .footer__upper {
    display: grid;

    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px;
  }
}

@media(min-width: $widthDesktop) {
  .footer__link {
    font-size: 18px;
  }

  .footer__tel {
    text-align: start;
  }

  .footer__logo {
    justify-content: flex-start;
  }

  .footer__callback {
    text-align: start;
  }
}
