.banner {
  position: relative;

  width: 100%;
  height: 300px;

  background: url('../img/main.jpg') center center no-repeat;
  background-size: cover;

  &::before {
    position: absolute;

    width: 100%;
    height: 100%;

    content: '';

    background: rgba(43, 43, 43, 0.5);
  }
}

.banner__title {
  margin: 0;
  margin-bottom: 30px;

  font-size: 30px;
  font-weight: 700;
  line-height: 34.7px;

  color: $white;
}

.banner__button {
  display: none;

  min-width: 220px;
}

.banner__container {
  position: absolute;
  z-index: 1;

  display: flex;

  max-width: 311px;
  height: 100%;

  align-items: center;
}

@media(min-width:$widthTablet) {
  .banner__title {
    margin-bottom: 44px;

    font-size: 60px;
    line-height: 70px;
  }

  .banner {
    height: 540px;
  }

  .banner__container {
    max-width: 612px;
  }
}

.banner__button {
  min-width: 240px;
}
