.chosen__cards {
  display: flex;

  gap: 16px;
  flex-wrap: wrap;
}

.chosen__container {
  display: flex;

  flex-wrap: wrap;
  gap: 50px;
}

.chosen__card {
  position: relative;

  overflow: hidden;

  width: 162px;
  min-height: 216px;

  border: 1px solid $black;
  border-radius: 10px;

  &--empty {
    position: relative;

    display: flex;

    cursor: pointer;

    border: none;
    background-color: #f2f2f2;

    align-items: center;
    justify-content: center;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &::before {
      display: flex;

      width: 50px;
      height: 50px;

      content: '';

      background: url('../img/plus.svg') 0 0 no-repeat;
      background-size: cover;

      align-items: center;
      justify-content: center;
    }
  }
}

.chosen__cross {
  position: absolute;
  top: 10px;
  right: 10px;

  padding: 0;

  cursor: pointer;

  border: none;
  background-color: transparent;

  &:hover,
  &:focus {
    svg {
      stroke: $yellow;
    }
  }

  svg {
    stroke: $white;
  }
}

.chosen__upper {
  padding: 11.28px 10px;

  color: $white;
  background-color: $black;

  span {
    font-weight: 700;
  }
}

.chosen__lower {
  padding: 11.28px 10px;
}

.chosen__subtitle {
  margin: 0;
  margin-bottom: 10.7px;

  font-size: 18px;
  font-weight: 700;
}

.chosen__picture {
  display: flex;

  width: 38px;
  height: 38px;
  margin: 0;
  margin-bottom: 5px;

  border-radius: 50%;
  background-color: $yellow;

  align-items: center;
  justify-content: center;
}

.chosen__feature {
  margin: 0;
  margin-bottom: 5px;

  line-height: 1.2em;

  &:last-of-type {
    margin-bottom: 0;
  }

  span {
    display: block;

    color: #8a8a8a;
  }
}

.chosen__price {
  margin: 0;
  margin-bottom: 50px;

  font-size: 35px;
  font-weight: 700;
  line-height: 1em;
}

.chosen__button {
  width: 100%;
  max-width: 340px;
}

@media(min-width:$widthDesktop) {
  .chosen__container {
    display: grid;

    grid-template-columns: 2fr 1fr;
  }

  .chosen__button {
    max-width: 200px;
  }

  .chosen__price {
    max-width: 240px;
    margin-bottom: 31px;

    line-height: 1.2em;
  }

  .chosen__card {
    width: 200px;
    max-width: 100%;

    &--empty {
      &::before {
        width: 80px;
        height: 80px;
      }
    }
  }

  .chosen__picture {
    width: 47px;
    height: 47px;
  }

  .chosen__subtitle {
    font-size: 24px;
  }

  .chosen__upper span {
    font-size: 18px;
  }

  .chosen__feature {
    margin-bottom: 10px;
  }

  .chosen__upper {
    padding: 14px 16px;
  }

  .chosen__lower {
    padding: 22px 16px;
  }
}
