.footer-nav__list {
  margin-bottom: 30px;
}

.footer-nav__item {
  display: block;

  margin-bottom: 30px;

  text-align: center;
  text-decoration: none;
  text-transform: uppercase;

  font-weight: 700;

  color: #e1e1e1;

  &:last-of-type {
    margin-bottom: 0;
  }

  &:hover,
  &:focus {
    color: $yellow;
  }
}

@media(min-width: $widthTablet) {
  .footer-nav__item {
    text-align: start;
  }
}

@media(min-width: $widthDesktop) {
  .footer-nav__item {
    font-size: 18px;
  }
}
