  *,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  -webkit-tap-highlight-color: transparent;
}

html {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;

  margin: 0;

  font-family: $play;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4em;

  color: $black;

  justify-content: space-between;
}

img {
  max-width: 100%;
  height: auto;
}

.main {
  flex-grow: 1;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;

  overflow: hidden;
  clip: rect(0, 0, 0, 0);

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  white-space: nowrap;

  border: 0;

  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 10px;
}

.title {
  margin: 0;
  margin-bottom: 30px;

  font-size: 24px;
  font-weight: 700;
  line-height: 27.77px;

  color: $black;
}

.yandex-map,
.yandex-map .iframe {
  width: 100%;
  height: 620px;
  margin: 0 auto;
  padding: 0;
}

.no-scroll {
  overflow: hidden;
}

.button {
  display: flex;

  padding: 14px 24px;

  cursor: pointer;

  font-family: $play;
  font-size: 18px;

  color: $black;
  border: none;
  border-radius: 6px;
  border-radius: 5px;
  background-color: $yellow;

  justify-content: center;
  align-items: center;

  &:hover,
  &:focus,
  &:active {
    color: $black;
    background-color: #fea81d;
  }
}

.radio-input {
  position: absolute;

  opacity: 0;

  flex-shrink: 0;

  &:checked + .radio-indicator {
    background: url(../img/radio-checked.svg) 0 0 no-repeat;
  }
}

.radio-indicator {
  display: block;

  width: 20px;
  height: 20px;
  margin-right: 8px;

  cursor: pointer;

  background: url(../img/radio.svg) 0 0 no-repeat;
}

.lg-backdrop.in {
  opacity: 0.7!important;
}

.inner-content {
  .title,
  h1 {
    font-size: 26px;
    font-weight: 800;
  }

  h2 {
    font-size: 20px;
    font-weight: 800;
  }

  h3 {
    font-size: 18px;
    font-weight: 800;
  }

  h4 {
    font-size: 18px;
    font-weight: 600;
  }

  h5 {
    font-size: 14px;
    font-weight: 700;
  }

  p {
    margin: 1em 0;

    font-size: 14px;
    line-height: 18.2px;
  }

  a {
    text-decoration: none;

    color: $yellow;
  }

  .button {
    color: $white;
  }
}

.input {
  width: 100%;
  padding: 8.5px 14px;

  border: 0;
  border-radius: 50px;
  background-color: #f2f2f2;
}

.tel {
  text-decoration: none;

  font-size: 20px;
  font-weight: 700;

  color: $black;
}

.overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  background: rgba(43, 43, 43, 0.5);
}

.overflow-hidden {
  overflow: hidden;
}

.section {
  margin-bottom: 50px;
}

@media(min-width:$widthTablet) {
  .container {
    padding: 0 20px;
  }

  .tel {
    font-size: 24px;
  }

  .title {
    font-size: 36px;
    line-height: 41.65px;
  }

  .section {
    margin-bottom: 100px;
  }
}

button {
  font-family: $play;
}

.ymaps-2-1-79-balloon__content {
  padding: 22px!important;
  padding-right: 0;
}

.free {
  cursor: pointer;

  background-color: #bfd5f4;

  fill: #bfd5f4;
}

.arenda {
  cursor: pointer;

  background-color: #f2f2f2;

  fill: #f2f2f2;
}

.process {
  cursor: pointer;

  background-color: #fbd972;

  fill: #fbd972;
}

.taken {
  cursor: pointer;

  background-color: #6fcf97;

  fill: #6fcf97;
}

path[class] {
  &:hover {
    fill: rgb(158, 157, 157);
  }
}
