.breadcrumbs {
  position: relative;

  width: 100%;
  height: 100px;
  margin-bottom: 20px;

  &::before {
    position: absolute;

    width: 100%;
    height: 100%;

    content: '';

    background-color: rgba(43, 43, 43, 0.5);
  }
}

.breadcrumbs__list {
  display: flex;

  padding-top: 30px;
  padding-left: 0;

  list-style: none;

  align-items: center;
  gap: 15px;
}

.breadcrumbs__item {
  position: relative;

  display: none;

  &:first-of-type {
    padding-left: 13px;

    &::before {
      position: absolute;
      left: 0;

      content: '<';

      color: $white;
    }
  }

  a {
    text-decoration: none;

    color: $white;
  }

  &--back {
    display: block;
  }

  &:last-of-type {
    &::before {
      display: none;
    }
  }
}

@media(min-width:$widthTablet) {
  .breadcrumbs {
    height: 190px;
    margin-bottom: 50px;
  }

  .breadcrumbs__item {
    display: block;

    &--back {
      display: none;
    }

    &::before {
      position: absolute;
      top: 0;
      right: -10px;

      content: '/';

      color: $white;
    }
  }
}
