.header {
  width: 100%;

  transition: all 0.3s ease-in;

  background-color: $white;

  &--scroll {
    position: fixed;
    z-index: 5;

    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
  }
}

.header__burger {
  border: none;
  background: transparent;

  &.hidden {
    display: none;
  }
}

.header__cross {
  z-index: 13;

  border: none;
  background: transparent;

  &.hidden {
    display: none;
  }

  svg {
    stroke: $black;
  }
}

.header__left {
  display: flex;

  align-items: center;
  justify-content: space-between;
}

.header__tel {
  display: none;

  &:hover,
  &:focus {
    color: #fea81d;
  }
}

.header__login {
  display: flex;

  margin-left: auto;
  padding: 18px 20px 18px 11px;

  font-family: $play;
  font-size: 18px;
  font-weight: 700;

  border: none;
  background-color: $yellow;

  align-items: center;

  svg {
    margin-right: 6px;

    fill: none;
  }

  &:hover,
  &:focus {
    cursor: pointer;

    background-color: #fea81d;
  }
}

.header__upper {
  padding: 5px 0;
}

.header__callback {
  padding: 12.5px 32.5px;

  font-size: 18px;

  border: 1px solid $black;
  border-radius: 6px;
  background-color: $white;

  &:hover,
  &:focus {
    cursor: pointer;

    border-color: $yellow;
    background-color: $yellow;
  }
}

.header__logo {
  max-height: 55px;
}

@media(max-width:767px) {
  .header__logo {
    display: flex;

    align-items: center;

    svg {
      width: 232px;
      height: auto;
    }
  }

  .header__callback {
    display: none;
  }
}

@media(min-width:$widthTablet) {
  .header--scroll {
    .header__upper {
      padding: 5px 0;
    }

    .header__lower {
      display: none;
    }
  }

  .header__cross,
  .header__burger {
    display: none;
  }

  .header__tel {
    display: block;

    margin-right: 26px;
  }

  .header__upper {
    display: flex;

    padding-top: 14px;
    padding-bottom: 14px;

    gap: 10px;
    justify-content: space-between;
    align-items: center;
  }

  .header__lower {
    padding-bottom: 10px;
  }

  .header__tels {
    display: flex;

    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;
    gap: 10px;
  }

  .header__login {
    margin: 0;
    padding: 5px 17px;

    border-radius: 6px;

    order: 1;
  }
}
