.choose__link-back {
  display: flex;

  margin-bottom: 50px;

  text-decoration: none;

  font-size: 14px;

  color: $black;

  align-items: center;

  span {
    display: flex;

    width: 24px;
    height: 24px;
    margin-right: 10px;

    border-radius: 50%;
    background-color: $yellow;

    align-items: center;
    justify-content: center;

    svg {
      fill: none;
    }
  }
}

.choose__text {
  display: block;

  margin-bottom: 20px;

  font-size: 18px;
  font-weight: 700;
}

.choose__list {
  display: grid;

  margin-bottom: 50px;

  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

.choose__item {
  display: block;

  width: 100%;
  padding: 11px;

  text-align: center;
  text-decoration: none;

  font-size: 14px;
  font-weight: 400;
  line-height: 1em;

  color: $black;
  border: 1px solid $black;
  border-radius: 6px;

  &.active {
    color: $white;
    background-color: $black;
  }
}

.choose__link-back {
  svg {
    stroke: $black;
  }
}

.choose__date {
  margin-bottom: 50px;

  span {
    margin: 0 5px;
  }
}

.choose__date-text {
  margin: 0;
  margin-bottom: 20px;

  font-size: 18px;
}

.choose__date-input {
  cursor: pointer;

  font-family: $play;

  border: none;
  border-bottom: 1px solid $black;
}

.choose__date-container {
  display: flex;

  margin-bottom: 60px;

  align-items: center;
}

.choose__submit {
  width: 100%;
  max-width: 340px;
  padding: 14px;

  text-align: center;

  font-size: 18px;

  border: none;
  border-radius: 6px;
  background-color: $yellow;
}

.choose__places {
  display: flex;

  align-items: center;
  gap: 30px;
}

.choose__place {
  display: flex;

  align-items: center;
}

.choose__number {
  display: flex;

  width: 40px;
  height: 40px;
  margin-right: 10px;

  border-radius: 6px;

  align-items: center;
  justify-content: center;
}

.choose__statistics {
  display: none;
}

.choose__floors {
  display: flex;
  display: none;

  font-size: 18px;

  gap: 36px;
}

.choose__floor {
  text-decoration: none;

  color: #828282;

  &.active {
    position: relative;

    color: $black;

    &::before {
      position: absolute;
      bottom: -8px;
      left: 50%;

      width: 21px;
      height: 4px;

      content: '';
      transform: translateX(-50%);

      background-color: $yellow;
    }
  }
}

.choose__scheme {
  position: relative;

  max-width: 370px;

  transition: max-width 1s ease-in;

  svg {
    width: 100%;
    height: auto;
  }
}

.choose__zoom {
  position: absolute;
  z-index: 2;
  right: 5px;
  bottom: 15px;

  display: flex;

  width: 30px;
  height: 30px;

  border: none;
  border-radius: 50%;
  background-color: $yellow;

  align-items: center;
  justify-content: center;
}

.choose__scheme.zoom {
  max-width: 100%;
  height: 500px;

  .choose__svg-container {
    position: absolute;

    overflow: scroll;
    overflow: scroll;

    width: 100%;
    height: 100%;

    svg {
      width: auto;
      height: 100%;
    }
  }
}

@media(min-width:$widthTablet) {
  .choose__type {
    display: flex;

    align-items: center;
  }

  .choose__scheme {
    height: auto;
    padding-top: 40px;
  }

  .choose__list {
    display: flex;

    gap: 10px;
  }

  .choose__item {
    width: fit-content;

    &:hover,
    &:focus {
      color: $white;
      background-color: $black;
    }
  }

  .choose__list {
    margin-bottom: 0;
  }

  .choose__text {
    margin-right: 16px;
    margin-bottom: 0;
  }

  .choose__date-text {
    display: none;
  }

  .choose__container {
    display: flex;

    padding: 30px 0;

    border-top: 1px solid rgba(225, 225, 225, 1);
    border-bottom: 1px solid rgba(225, 225, 225, 1);

    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
  }

  .choose__date {
    margin-bottom: 0;
  }

  .choose__submit {
    display: none;
  }

  .choose__date-container {
    margin-bottom: 0;
  }

  .choose__statistics {
    display: flex;

    padding-top: 40px;

    align-items: center;
    justify-content: flex-end;
    flex-wrap: wrap;
    gap: 20px;
  }

  .choose__zoom {
    display: none;
  }
}

@media(min-width:$widthDesktop) {
  .choose__link-back {
    font-size: 18px;
    font-weight: 700;
  }

  .choose__container {
    gap: 42px;
  }

  .choose__item {
    font-size: 16px;
  }

  .choose__scheme {
    max-width: 100%;
  }
}
