.gallery__items {
  display: flex;
  flex-direction: column;

  gap: 20px;
}

.gallery__item {
  display: block;

  width: 100%;

  img {
    width: 100%;
    height: auto;

    object-fit: contain;
  }
}

.gallery__nav {
  transition: 0.7s ease-in opacity;

  opacity: 1;

  &.hidden {
    position: absolute;
    left: -100%;

    opacity: 0;
  }
}

.gallery__panes {
  display: flex;

  margin-bottom: 40px;

  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

.gallery__pane {
  padding: 10px 24px;

  font-size: 16px;
  line-height: 1em;

  color: $black;
  border: 2px solid #2b2b2b;
  border-radius: 6px;
  background-color: transparent;

  &.active {
    color: $white;
    background-color: $black;
  }
}

@media(min-width:500px) {
  .gallery__items {
    display: grid;

    grid-template-columns: 1fr 1fr;
  }
}

@media(min-width:800px) {
  .gallery__items {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
  }
}
