.map__map {
  width: 100%;
  height: 500px;

  background: #e5ffe5;
}

.map__title {
  display: block;

  margin-bottom: 23px;

  text-decoration: none;

  font-family: $play;
  font-size: 24px;
  font-weight: 700;

  color: $black;
}

.map__link {
  display: block;

  width: fit-content;
  padding: 10.5px 55px;

  text-decoration: none;

  font-family: $play;
  font-size: 16px;

  color: $black;
  border-radius: 6px;
  background-color: $yellow;

  &:hover,
  &:focus {
    background-color: #fea81d;
  }
}

.map__places {
  margin-bottom: 10px;

  font-family: $play;
  font-size: 14px;
}

.map__place {
  display: inline;

  margin-right: 20px;

  &:last-of-type {
    margin-right: 0;
  }

  .free {
    margin-right: 5px;
    padding: 7px 2px;

    border-radius: 6px;
    background-color: #bfd5f4;
  }

  .taken {
    margin-right: 5px;
    padding: 7px 2px;

    border-radius: 6px;
    background-color: #f2f2f2;
  }
}

.map__price {
  margin-bottom: 30px;

  font-family: $play;
  font-size: 14px;
}

@media(min-width:$widthTablet) {
  .map__map {
    height: 620px;
  }
}
