.nav__item {
  display: block;

  padding-right: 22px;
  padding-bottom: 30px;
  padding-left: 22px;

  text-decoration: none;

  font-size: 18px;
  font-weight: 700;

  color: $black;

  &:last-of-type {
    margin-bottom: 0;
  }

  &:hover,
  &:focus {
    color: #fea81d;
  }
}

.nav__tel {
  display: none;
}

.nav.hidden {
  .nav__list {
    left: 200%;

    transition: all 1s ease-in;
  }

  .overlay {
    display: none;
  }
}

.nav.show {
  .nav__list {
    left: 0;
  }
}

.nav__list {
  position: absolute;
  top: 0;
  left: 0;

  transition: all 0.5s ease-in;

  &.hidden {
    left: -200%;
  }
}

.nav {
  top: 65px;

  width: 100%;
  height: 100%;

  transition: all 1s;

  background: $white;

  .overlay.hidden {
    display: none;
  }
}

.nav__list {
  position: absolute;
  z-index: 12;

  max-width: 261px;
  height: 100vh;

  background-color: $white;
}

@media(max-width:767px) {
  .nav__tel {
    display: block;

    width: 100%;
    margin-bottom: 20px;
    padding: 15px 0 15px 20px;

    border-top: 1px solid #f2f2f2;
    border-bottom: 1px solid #f2f2f2;
  }
}

@media(min-width:$widthTablet) {
  .nav {
    .overlay {
      display: none;
    }
  }

  .nav__list.hidden {
    position: initial;

    display: flex;

    max-width: 100%;
    height: auto;

    transition: none;

    gap: 10px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
  }

  .nav__item {
    margin: 0;
    padding: 14px;

    &:nth-of-type(2) {
      padding-left: 0;
    }

    &:last-of-type {
      padding-right: 0;
    }
  }
}

@media(min-width:$widthTablet) {
  .nav__list.hidden {
    justify-content: space-between;
  }
}
