.choose-main__link {
  position: relative;

  display: block;

  width: 100%;
  min-height: 160px;
  margin-bottom: 16px;
  padding: 20px;

  text-decoration: none;

  color: $white;
  border-radius: 6px;
  background-color: #828282;

  &::before {
    position: absolute;
    right: 0;
    bottom: 0;

    width: 188px;
    height: 104px;

    content: '';

    background: url('../img/car-1.png') 0 0 no-repeat;
    background-size: cover;
  }

  &:nth-of-type(1) {
    background-color: #a8bbd4;

    &::before {
      background: url('../img/car-1.png') 0 0 no-repeat;
    }
  }

  &:nth-of-type(2) {
    background-color: #f3a9a9;

    &::before {
      background: url('../img/car-2.png') 0 0 no-repeat;
    }
  }

  &:nth-of-type(3) {
    color: $black;
    background-color: #fbd972;

    &::before {
      background: url('../img/car-3.png') 0 0 no-repeat;
    }
  }

  &:nth-of-type(4) {
    background-color: #828282;

    &::before {
      background: url('../img/car-4.png') 0 0 no-repeat;
    }
  }

  &:nth-of-type(5) {
    background-color: #6fcf97;
  }
}

.choose-main__title {
  margin: 0;
  margin-bottom: 10px;

  font-size: 18px;
  font-weight: 700;
  line-height: 20.8px;
}

.choose-main__text {
  margin: 0;

  font-size: 16px;
  line-height: 18.5px;
}

@media(min-width:$widthTablet) {
  .choose-main__list {
    display: grid;

    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
  }

  .choose-main__link {
    height: 311px;
    margin: 0;

    &::before {
      width: 370px;
      height: 204px;

      background: url('../img/car-1-d.png') 0 0 no-repeat;
    }

    &:nth-of-type(1) {
      &::before {
        background: url('../img/car-1-d.png') 0 0 no-repeat;
      }
    }

    &:nth-of-type(2) {
      &::before {
        background: url('../img/car-2-d.png') 0 0 no-repeat;
      }
    }

    &:nth-of-type(3) {
      &::before {
        width: 335px;

        background: url('../img/car-3-d.png') 0 0 no-repeat;
      }
    }

    &:nth-of-type(4) {
      &::before {
        background: url('../img/car-4-d.png') 0 0 no-repeat;
      }
    }
  }

  .choose-main__title {
    max-width: 370px;

    font-size: 30px;
    line-height: 1em;

    span {
      display: block;
    }
  }

  .choose-main__text {
    font-size: 23px;
  }
}

@media(min-width:$widthDesktop) {
  .choose-main__list {
    grid-column-gap: 40px;
    grid-row-gap: 35px;
  }
}
