.parking {
  position: relative;

  height: 270px;

  background-position: center center;
  background-size: cover;

  &::before {
    position: absolute;

    width: 100%;
    height: 100%;

    content: '';

    background: rgba(43, 43, 43, 0.5);
  }

  & > .container {
    position: relative;
    z-index: 1;

    display: flex;
    flex-direction: column;

    height: 100%;
    padding: 27px 10px;

    justify-content: center;
  }
}

.parking__link-back {
  position: absolute;
  top: 27px;

  display: flex;

  text-decoration: none;

  color: $white;

  align-items: center;

  &:hover,
  &:focus {
    color: $yellow;

    svg {
      stroke: $yellow;
    }
  }

  svg {
    margin-right: 6px;

    stroke: $white;
    fill: none;
  }
}

.parking__title {
  margin: 0;
  margin-bottom: 16px;

  font-size: 30px;
  line-height: 1em;

  color: $white;
}

.parking__tel {
  text-decoration: none;

  font-size: 24px;

  color: $white;
}

@media(min-width:$widthDesktop) {
  .parking {
    height: 400px;
  }

  .parking__title {
    font-size: 45px;
  }

  .parking__link-back {
    top: 31px;
  }
}
