.photo__container {
  position: relative;

  display: flex;

  gap: 20px;
  align-items: center;
}

.photo__prev {
  position: relative;

  display: flex;

  width: 50px;
  height: 50px;

  cursor: pointer;

  border: none;
  border-radius: 50%;
  background: $yellow;

  flex-shrink: 0;
  align-items: center;
  justify-content: center;

  &:hover,
  &:focus {
    background-color: $black;

    svg {
      stroke: $yellow;
    }
  }

  svg {
    fill: none;
    stroke: $black;
  }
}

.photo__next {
  position: relative;

  display: flex;

  width: 50px;
  height: 50px;

  cursor: pointer;

  border: none;
  border-radius: 50%;
  background: $yellow;

  flex-shrink: 0;
  align-items: center;
  justify-content: center;

  &:hover,
  &:focus {
    background-color: $black;

    svg {
      stroke: $yellow;
    }
  }

  svg {
    fill: none;
    stroke: $black;
  }
}
