.price__table {
  width: 100%;

  border-collapse: collapse;

  border-top: 1px solid rgba(225, 225, 225, 1);
  border-bottom: 1px solid rgba(225, 225, 225, 1);
}

.price__header {
  .price__td {
    font-weight: 700;
  }
}

.price__td {
  padding: 14px 16px;
}

.price__tr {
  .price__td:first-of-type {
    font-weight: 700;
  }

  &:nth-of-type(2n) {
    background-color: rgba(242, 242, 242, 1);
  }
}

@media(min-width:$widthDesktop) {
  .price__td {
    padding: 13px 20px;

    font-size: 20px;
  }
}
