.service__list {
  display: flex;
  flex-direction: column;

  gap: 10px;
}

.service__item {
  position: relative;

  width: 100%;
  padding: 30px;
  padding-bottom: 40px;
  padding-left: 120px;

  border: 1px solid #2b2b2b;
  border-radius: 10px;

  &::before {
    position: absolute;
    top: 30px;
    left: 20px;

    width: 80px;
    height: 80px;

    content: '';

    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: contain;
  }

  &:nth-of-type(1) {
    &::before {
      background-image: url('../img/icon-service-1.svg');
    }
  }

  &:nth-of-type(2) {
    &::before {
      background-image: url('../img/icon-service-2.svg');
    }
  }

  &:nth-of-type(3) {
    &::before {
      background-image: url('../img/icon-service-3.svg');
    }
  }

  &:nth-of-type(4) {
    &::before {
      background-image: url('../img/icon-service-4.svg');
    }
  }
}

.service__subtitle {
  margin: 0;
  margin-bottom: 14px;

  font-size: 20px;
  font-weight: 700;
  line-height: 1.1em;
}

.service__text {
  margin: 0;
  margin-bottom: 20px;

  font-size: 16px;
  line-height: 18.5px;

  color: rgba(106, 106, 106, 1);
}

.service__price {
  font-size: 25px;
  font-weight: 700;
  line-height: 1em;

  color: $black;
}

@media(min-width:$widthTablet) {
  .service__list {
    display: grid;

    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }

  .service__item {
    padding-left: 195px;

    &::before {
      left: 30px;

      width: 120px;
      height: 120px;
    }
  }

  .service__subtitle {
    font-size: 28px;
  }

  .service__price {
    font-size: 35px;
  }
}
