.local-map__map {
  width: 100%;
  height: 400px;

  background-color: lightblue;

  .iframe {
    width: 100%;
    height: 100%;
  }
}

.local-map__address {
  margin: 0;
  margin-bottom: 30px;

  font-size: 18px;
}

.local-map__title {
  margin-bottom: 20px;
}

@media(min-width:$widthTablet) {
  .local-map__header {
    display: flex;

    margin-bottom: 50px;

    align-items: center;
    justify-content: space-between;
  }

  .local-map__title {
    margin-bottom: 0;
  }

  .local-map__map {
    height: 474px;
  }

  .local-map__address {
    margin-bottom: 0;

    font-size: 24px;
    font-weight: 700;
  }
}
